/* selector */
.selector {
  width: 252px;
  height: 48px;
  padding: 12px 16px;
  box-sizing: border-box;
  background: rgba(128, 131, 164, 0.06);
  border: 2px solid rgba(128, 131, 164, 0.2);
  border-radius: 8px;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #2C2D76;;
  cursor: pointer;
  text-transform: capitalize;
}

.selector__city-option {
  text-transform: capitalize;
}